/** @format */

table {
  width: 200px;
  height: 200px;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
td {
  width: 20px;
  height: 20px;
  background-color: white;
  text-align: center;
  font-weight: bold;
}
.active {
  background-color: black;
}
